  position: relative;
}
@media (min-width: 768px) {
  .main-nav__inner {
    display: none;
  }
}
.main-nav__button {
  position: absolute;
  top: -45px;
  right: 0;
  width: 26px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__button {
    display: none;
  }
}
/*бургер в меню*/
.main-nav--closed .main-nav__inner::before {
  position: absolute;
  top: -33px;
  right: 0;
  display: block;
  content: "";
  width: 25px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 -8px 0 0 #000000, 0 8px 0 0 #000000;
}
/*крестик в меню*/
.main-nav--opened .main-nav__inner::before,
.main-nav--opened .main-nav__inner::after {
  display: block;
  content: "";
  position: absolute;
  top: -35px;
  right: 0;
  width: 26px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__inner::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__inner::after {
  transform: rotate(-45deg);
}
/*свертывание меню в мобильной версии*/
@media (max-width: 767px) {
  .main-nav--closed .main-nav__menu-list {
    display: none;
  }
}
.main-nav--nojs .main-nav__button {
  display: none;
}
.main-nav--nojs .main-nav__menu-list {
  position: static;
}
.main-nav__logo-link:hover {
  opacity: 0.8;
}
.main-nav__logo-link:active {
  opacity: 0.6;
}
.main-nav__link:hover {
  opacity: 0.6;
}
