.switcher__label {
  position: relative;
  display: inline-block;
  max-width: 100px;
  word-wrap: break-word;
  cursor: pointer;
}
.switcher__label::before,
.switcher__label::after {
  position: absolute;
  display: block;
  content: "";
  z-index: 10;
}
.switcher__label::before {
  top: -13px;
  left: 97px;
  width: 86px;
  height: 14px;
  background: #ffffff;
  border: 1px solid #CDCDCD;
  box-sizing: border-box;
  border-radius: 6px;
}
