  .switcher__label::before {
    top: -2px;
    left: 110px;
    width: 426px;
    height: 6px;
    background-color: #CDCDCD;
  }
}
.switcher__label::after {
  top: -9px;
  left: 103px;
  width: 36px;
  height: 6px;
  border-radius: 6px;
  background-color: #68b738;
  transition: 200ms ease-in;
}
@media (min-width: 768px) {
  .switcher__label::after {
    left: 320px;
    top: -5px;
    width: 6px;
    height: 6px;
    border: 1px solid #CDCDCD;
    border-radius: 50%;
    box-shadow: 0 0 0 11px #ffffff, 0 0 0 12px #00000029;
  }
}
@media (min-width: 768px) {
  .switcher__label:hover::after {
    box-shadow: 0 0 0 11px #ffffff, 0 0 0 12px #CDCDCD, 0 2px 4px 12px #00000030;
  }
}
@media (min-width: 768px) {
  .switcher__label:active::after {
    box-shadow: 0 0 0 11px #ffffff, 0 0 0 12px #68b738, 0 3px 5px 12px #00000033;
  }
}
